function Toggle() {
    return (
        <div className="radio-wrapper">
            <input type="radio" name="theme" className="theme-1" id="radio-1" />
            <label for="radio-1">1</label>

            <input type="radio" name="theme" className="theme-2" id="radio-2" />
            <label for="radio-2">2</label>

            <input type="radio" name="theme" className="theme-3" id="radio-3" />
            <label for="radio-3">3</label>

            <div className="selector">&nbsp;</div>

        </div>)
}

export default Toggle;